<template>
  <div class="cont">
    <div class="header">
      <h2 v-if="action === 'addExam'" class="text-center">أضافة أمتحان</h2>
      <h2 v-if="action === 'editExam'" class="text-center">تعديل الأمتحان</h2>
      <!-- <h5 class="text-center" v-if="action === 'editExam'">{{ exam.number }}</h5> -->
      <hr />
    </div>
    <div class="settings _inputs1">
      <div v-if="!exam.isTask" class="row" dir="rtl">
        <div class="col-md-3">
          <div class="form-group form-text">
            <label for="year">السنة الدراسية</label>
            <input
              id="year"
              class="form-control"
              min="2020"
