              v-model.number="exam.year"
            />
            <span>/{{ exam.year + 1 }}</span>
          </div>
        </div>
        <div class="col-md-3">
          <div class="form-group">
            <label>المرحلة الدراسية</label>
            <select class="form-control" v-model="exam.stage">
              <option
                v-for="stage in $store.getters.stages"
