                :value="stage.value"
              >
                {{ stage.text }}
              </option>
            </select>
          </div>
        </div>
        <!--------------------------UNIT----------------------------->
        <div class="col-md-3">
          <div class="form-group">
            <label>الوحدة</label>
            <select class="form-control" v-model="exam.unit">
              <option
                v-for="unit in units"
                :key="unit.number"
                selected
                :value="unit.number"
              >
                {{ unit.name }}
              </option>
            </select>
          </div>
        </div>
        <!------------------------------------------------>
        <div class="col-md-3">
          <div class="form-group">
            <label>أسم الامتحان</label>
            <input class="form-control" type="text" v-model="exam.name" />
